:host {
  display: block;
}

.message-wrapper {
  display: flex;
  margin-bottom: 0.5rem;
  padding: 0 1rem;
  animation: slideIn 0.2s ease-out;

  // User message - primary color bubble on right
  &.user-message {
    justify-content: flex-end;

    .message-bubble {
      background-color: var(--color-primary-color, #3b82f6);
      border-radius: 0.5rem 0.5rem 0 0.5rem;
      max-width: 75%;
      margin-left: 3rem;
      position: relative;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08);

      // Tail triangle
      &::after {
        content: '';
        position: absolute;
        right: -8px;
        bottom: 0;
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-left-color: var(--color-primary-color, #3b82f6);
        border-bottom-color: var(--color-primary-color, #3b82f6);
        border-right: 0;
        border-bottom: 0;
      }

      .message-content {
        color: var(--color-primary-contrast, #ffffff);
      }

      .message-time {
        color: rgba(255, 255, 255, 0.7);
      }
    }
  }

  // Assistant message - card background on left
  &.assistant-message {
    justify-content: flex-start;

    .message-bubble {
      background-color: var(--card-background-color, #ffffff);
      border-radius: 0.5rem 0.5rem 0.5rem 0;
      max-width: 85%;
      margin-right: 3rem;
      position: relative;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
      border: 1px solid var(--input-border-color, #e2e8f0);

      // Tail triangle
      &::after {
        content: '';
        position: absolute;
        left: -8px;
        bottom: 0;
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-right-color: var(--card-background-color, #ffffff);
        border-bottom-color: var(--card-background-color, #ffffff);
        border-left: 0;
        border-bottom: 0;
      }

      .message-content {
        color: var(--text-color, #1e293b);
      }

      .message-time {
        color: var(--text-color-secondary, #64748b);
      }
    }
  }

  // System message - centered, subtle
  &.system-message {
    justify-content: center;
    padding: 0.25rem 1rem;

    .message-bubble {
      background-color: var(--surface-hover, rgba(225, 245, 254, 0.92));
      border-radius: 0.5rem;
      max-width: 90%;
      text-align: center;
      font-size: 0.8125rem;
      color: var(--text-color-secondary, #64748b);
      padding: 0.375rem 0.75rem;
      box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);

      &::after {
        display: none;
      }
    }
  }
}

.message-bubble {
  padding: 0.5rem 0.625rem 0.375rem;
  position: relative;
  word-wrap: break-word;
}

.message-content {
  font-size: 0.9375rem;
  line-height: 1.4;
  white-space: pre-wrap;
  word-break: break-word;

  // Markdown-style formatting
  :host ::ng-deep {
    strong,
    b {
      font-weight: 600;
    }

    em,
    i {
      font-style: italic;
    }

    code {
      background-color: rgba(0, 0, 0, 0.06);
      padding: 0.125rem 0.375rem;
      border-radius: 0.25rem;
      font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
      font-size: 0.85em;
    }

    ul,
    ol {
      margin: 0.375rem 0;
      padding-left: 1.25rem;
    }

    li {
      margin-bottom: 0.125rem;
    }

    blockquote {
      margin: 0.375rem 0;
      padding-left: 0.625rem;
      border-left: 3px solid var(--color-primary-color, #3b82f6);
      color: var(--text-color-secondary, #64748b);
      font-style: italic;
    }

    p {
      margin: 0 0 0.375rem;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

// Interactive Components
.interactive-component {
  margin-top: 0.625rem;
  padding-top: 0.625rem;
  border-top: 1px solid var(--input-border-color, rgba(0, 0, 0, 0.06));
}

// Suggested Actions
.suggested-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 0.625rem;
  padding-top: 0.625rem;
  border-top: 1px solid var(--input-border-color, rgba(0, 0, 0, 0.06));

  &.multi-select-mode {
    flex-direction: column;
    gap: 0.75rem;
  }
}

// Multi-select mode styles
.multi-select-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.5rem;
}

.multi-select-label {
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--text-color-secondary, #64748b);
}

.select-all-btn {
  padding: 0.25rem 0.625rem;
  font-size: 0.75rem;
  font-weight: 500;
  background: transparent;
  border: 1px solid var(--input-border-color, #d1d7db);
  border-radius: 0.375rem;
  color: var(--text-color-secondary, #64748b);
  cursor: pointer;
  transition: all 0.15s ease;

  &:hover {
    background: var(--surface-hover, #f0f2f5);
    border-color: var(--input-border-color, #8696a0);
  }
}

.checkbox-actions {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.checkbox-action {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background: var(--surface-hover, #f0f2f5);
  border: 1.5px solid transparent;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.15s ease;
  user-select: none;

  &:hover {
    background: var(--surface-ground, #e9edef);
  }

  &.selected {
    background: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    border-color: var(--color-primary-color, #3b82f6);
  }

  input[type='checkbox'] {
    width: 1rem;
    height: 1rem;
    accent-color: var(--color-primary-color, #3b82f6);
    cursor: pointer;
  }
}

.checkbox-label {
  font-size: 0.8125rem;
  font-weight: 500;
  color: var(--text-color, #1e293b);
}

.multi-select-footer {
  margin-top: 0.25rem;
}

.submit-selection-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
  padding: 0.625rem 1rem;
  font-size: 0.875rem;
  font-weight: 600;
  background: var(--color-primary-color, #3b82f6);
  border: none;
  border-radius: 0.5rem;
  color: var(--color-primary-contrast, #ffffff);
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover:not(:disabled) {
    filter: brightness(0.9);
    transform: translateY(-1px);
    box-shadow: 0 4px 8px rgba(var(--color-primary-rgb, 59, 130, 246), 0.3);
  }

  &:disabled {
    background: var(--input-border-color, #d1d7db);
    cursor: not-allowed;
    transform: none;
    box-shadow: none;
  }

  i {
    font-size: 0.875rem;
  }
}

// Single-select action chips
.action-chip {
  padding: 0.5rem 1rem;
  font-size: 0.8125rem;
  font-weight: 500;
  background: var(--surface-hover, #f0f2f5);
  border: 1px solid var(--input-border-color, #d1d7db);
  border-radius: 1.25rem;
  color: var(--color-primary-color, #3b82f6);
  cursor: pointer;
  transition: all 0.15s ease;
  white-space: nowrap;

  &:hover {
    background: rgba(var(--color-primary-rgb, 59, 130, 246), 0.1);
    border-color: var(--color-primary-color, #3b82f6);
    transform: translateY(-1px);
  }

  &:active {
    transform: translateY(0);
  }
}

// Message footer
.message-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 0.25rem;
  margin-top: 0.125rem;
}

.message-time {
  font-size: 0.6875rem;
}

.message-status {
  font-size: 0.75rem;
  display: flex;
  align-items: center;

  &.sent,
  &.delivered {
    color: var(--text-color-secondary, #667781);
  }

  &.read {
    color: var(--color-primary-color, #3b82f6);
  }

  i {
    font-size: 0.75rem;
  }
}

// Animation
@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(6px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
